<template>
  <div class="return">
    <el-container>
      <el-main>
        <!--        -->
        <el-row class="return-header">
          <el-col :span="4" class="search-page-pane">
            <el-select
              v-model="pageSize"
              placeholder=""
              @change="changeSize"
              class="search-size"
            >
              <el-option
                v-for="item in sizeOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-col>
          <el-col :span="16" class="search-input-pane">
            <el-row>
              <el-col :span="18">
                <el-input
                  placeholder=""
                  v-model="searchInput"
                  :suffix-icon="Search"
                  class="search-input"
                >
                </el-input>
              </el-col>
              <el-col :span="6">
                <el-button
                  type="primary"
                  class="search-button"
                  @click="searchButton"
                >
                  
                </el-button>
              </el-col>
            </el-row>
          </el-col>
        </el-row>
        <!--        -->
        <el-row class="return-table">
          <el-col>
            <el-table :data="borrows" height="100%" empty-text="">
              <el-table-column fixed prop="id" label="Id" width="50" />
              <el-table-column prop="bookName" label="" />
              <el-table-column prop="isbn" label="ISBN" />
              <el-table-column prop="username" label="" />
              <el-table-column prop="idCard" label="" />
              <el-table-column prop="borrowTime" label="" />
              <el-table-column prop="returnTime" label="" />
              <el-table-column fixed="right" label="">
                <template #default="borrows">
                  <el-button @click="returnBookDialog(borrows.row)" type="text"
                    ></el-button
                  >
                </template>
              </el-table-column>
            </el-table>
            <el-pagination
              background
              v-model:current-page="pageNum"
              v-model:page-size="pageSize"
              layout="prev, pager, next"
              :total="pageTotal"
              @current-change="page"
            >
            </el-pagination>
          </el-col>
        </el-row>
        <!--        -->
        <el-dialog
          v-model="returnBookDialogVisible"
          title=""
          width="500px"
        >
          <span> {{ returnName }}</span>
          <template #footer>
            <span class="dialog-footer">
              <el-button @click="returnBookDialogVisible = false"
                ></el-button
              >
              <el-button type="primary" @click="returnBook">  </el-button>
            </span>
          </template>
        </el-dialog>
      </el-main>
    </el-container>
  </div>
</template>

<script lang="ts" setup>
import { ref } from "vue";
import { Search } from "@element-plus/icons-vue";
import axios from "axios";
import { ElMessageBox } from "element-plus";

// 
let borrows = ref();
const getBorrow = () => {
  axios
    .get("http://localhost:8888/borrow/" + pageNum.value + "/" + pageSize.value)
    .then((resp) => {
      borrows.value = resp.data.content;
      pageTotal.value = resp.data.totalElements;
    });
};

// 
let pageNum = ref(1);
let pageSize = ref(10);
let pageTotal = ref(0);
const page = (val: number) => {
  pageNum.value = val;
  if (searchInput.value == undefined) {
    getBorrow();
  } else {
    searchBorrow();
  }
};

// 
let sizeOptions = [
  {
    value: 10,
    label: "10/",
  },
  {
    value: 50,
    label: "50/",
  },
  {
    value: 100,
    label: "100/",
  },
];
// 
const changeSize = (value: number) => {
  pageSize.value = value;
  if (searchInput.value == undefined) {
    getBorrow();
  } else {
    searchBorrow();
  }
};

// 
const searchInput = ref();
// 
const searchButton = () => {
  pageNum.value = 1;
  searchBorrow();
};
// 
const searchBorrow = () => {
  if (searchInput.value != "") {
    axios
      .get(
        "http://localhost:8888/borrow/search/" +
          searchInput.value +
          "/" +
          pageNum.value +
          "/" +
          pageSize.value
      )
      .then((resp) => {
        borrows.value = resp.data.content;
        pageTotal.value = resp.data.totalElements;
      });
  } else {
    getBorrow();
  }
};

// 
let returnName = ref("");
let returnId = ref(0);
let returnBookDialogVisible = ref(false);
const returnBookDialog = (row: any) => {
  returnId.value = row.id;
  returnName.value = row.bookName;
  returnBookDialogVisible.value = true;
};
const returnBook = () => {
  if (returnId.value) {
    axios
      .post("http://localhost:8888/borrow/return/" + returnId.value)
      .then((resp) => {
        const statusCode = resp.data.statusCode;

        // 
        if (statusCode == 0) {
          ElMessageBox.alert("", "", {
            confirmButtonText: "",
          });
        }
        // 
        if (statusCode == 1) {
          ElMessageBox.alert("", "", {
            confirmButtonText: "",
            callback: () => {
              returnBookDialogVisible.value = false;
            },
          });
        }
        // 
        if (statusCode == 2) {
          ElMessageBox.alert("", "", {
            confirmButtonText: "",
          });
        }
      });
  }
};

// 
const init = () => {
  getBorrow();
};
init();
</script>

<style lang="scss">
@import "../assets/css/return";
</style>
