<template>
  <div class="home">
    <el-main>
      <!--      -->
      <el-row class="dashboard">
        <el-col :xs="24" :sm="12" :lg="6" class="book-num pane"
          ><div>
            <el-icon><Reading /></el-icon>
            <div class="message">
              <p class="title"></p>
              <p class="sub-title">{{ bookCount }}</p>
            </div>
          </div></el-col
        >
        <el-col :xs="24" :sm="12" :lg="6" class="overtime-num pane"
          ><div>
            <el-icon><Failed /></el-icon>
            <div class="message">
              <p class="title"></p>
              <p class="sub-title">{{ overtimeCount }}</p>
            </div>
          </div></el-col
        >
        <el-col :xs="24" :sm="12" :lg="6" class="borrow-num pane"
          ><div>
            <el-icon><List /></el-icon>
            <div class="message">
              <p class="title"></p>
              <p class="sub-title">{{ borrowCount }}</p>
            </div>
          </div></el-col
        >
        <el-col :xs="24" :sm="12" :lg="6" class="system-time pane"
          ><div>
            <el-icon><Timer /></el-icon>
            <div class="message">
              <p class="title"></p>
              <p class="time">{{ times }}</p>
            </div>
          </div></el-col
        >
      </el-row>
      <!--      -->
      <el-card class="log">
        <template #header>
          <span></span>
        </template>
        <el-table
          style="width: 100%"
          height="600"
          :data="logs"
          :default-sort="{ prop: 'date', order: 'descending' }"
        >
          <el-table-column
            prop="time"
            label="/"
            sortable
            width="220px"
          />
          <el-table-column prop="name" label="" width="120px" />
          <el-table-column prop="info" label="" />
        </el-table>
      </el-card>
    </el-main>
  </div>
</template>

<script lang="ts" setup>
import { ref } from "vue";
import { Reading, Failed, List, Timer } from "@element-plus/icons-vue";
import axios from "axios";

// 
let bookCount = ref(0);
let overtimeCount = ref(0);
let borrowCount = ref(0);
let times = ref("");

//
setInterval(function () {
  let date = new Date();
  let year = date.getFullYear(); //
  let mon = date.getMonth() + 1; //
  let da = date.getDate(); //
  let day = date.getDay(); //
  let h: any = date.getHours(); //
  let m: any = date.getMinutes(); //
  let s: any = date.getSeconds(); //
  let weeks = ["", "", "", "", "", "", ""]; //
  let d = document.getElementById("Date");
  if (h < 10) {
    h = "0" + h;
  }
  if (m < 10) {
    m = "0" + m;
  }
  if (s < 10) {
    s = "0" + s;
  }
  times.value =
    year +
    "-" +
    mon +
    "-" +
    da +
    " " +
    "" +
    weeks[day] +
    " " +
    h +
    ":" +
    m +
    ":" +
    s;
}, 1);

// 
const getDashboard = () => {
  axios.get("http://localhost:8888/book/dashboard/").then((resp) => {
    bookCount.value = resp.data.bookCount;
    overtimeCount.value = resp.data.overtimeCount;
    borrowCount.value = resp.data.borrowCount;
  });
};
getDashboard();

// 
let logs = ref();
const getLogs = () => {
  axios.get("http://localhost:8888/nav/logs/").then((resp) => {
    logs.value = resp.data.content;
  });
};
getLogs();
</script>

<style lang="scss">
@import "../assets/css/home";
</style>
