import { createRouter, createWebHistory, RouteRecordRaw } from "vue-router";
import AboutView from "@/views/AboutView.vue";
import TestPageView from "@/views/TestPageView.vue";

import LoginView from "@/views/LoginView.vue";
import NavView from "@/views/NavView.vue";
import HomeView from "@/views/HomeView.vue";
import BookView from "@/views/BookView.vue";
import BorrowView from "@/views/BorrowView.vue";
import ReturnView from "@/views/ReturnView.vue";
import OvertimeView from "@/views/OvertimeView.vue";
import UserView from "@/views/UserView.vue";
import SystemView from "@/views/SystemView.vue";
import RegisterView from "@/views/RegisterView.vue";

const routes: Array<RouteRecordRaw> = [
  {
    path: "/",
    name: "",
    component: LoginView,
    meta: { show: false },
  },
  {
    path: "/login",
    name: "",
    component: LoginView,
    meta: { show: false },
  },
  {
    path: "/register",
    name: "",
    component: RegisterView,
    meta: { show: false },
  },
  {
    path: "/nav",
    name: "",
    component: NavView,
    redirect: "/home",
    meta: { show: false },
    children: [
      {
        path: "/home",
        name: "",
        meta: { show: true },
        components: { home: HomeView },
      },
    ],
  },
  {
    path: "/nav",
    name: "",
    component: NavView,
    meta: { show: true },
    children: [
      {
        path: "/book",
        name: "",
        meta: { show: true },
        components: { book: BookView },
      },
      {
        path: "/borrow",
        name: "",
        meta: { show: true },
        components: { borrow: BorrowView },
      },
      {
        path: "/return",
        name: "",
        meta: { show: true },
        components: { return: ReturnView },
      },
      {
        path: "/overtime",
        name: "",
        meta: { show: true },
        components: { overtime: OvertimeView },
      },
    ],
  },
  {
    path: "/nav",
    name: "",
    component: NavView,
    meta: { show: true },
    children: [
      {
        path: "/user",
        name: "",
        meta: { show: true },
        components: { user: UserView },
      },
    ],
  },
  {
    path: "/nav",
    name: "",
    component: NavView,
    meta: { show: true },
    children: [
      {
        path: "/system",
        name: "",
        meta: { show: true },
        components: { system: SystemView },
      },
      {
        path: "/about",
        name: "",
        meta: { show: true },
        components: { about: AboutView },
      },
      {
        path: "/test",
        name: "",
        meta: { show: true },
        components: { test: TestPageView },
      },
    ],
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
