<template>
  <div class="book">
    <el-container>
      <el-main>
        <!--        -->
        <el-row class="book-header">
          <el-col :span="4" class="search-page-pane">
            <el-select
              v-model="pageSize"
              placeholder=""
              @change="changeSize"
              class="search-size"
            >
              <el-option
                v-for="item in sizeOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-col>
          <el-col :span="16" class="search-input-pane">
            <el-row>
              <el-col :span="4">
                <el-select
                  v-model="searchModel"
                  placeholder=""
                  @change="changeSearch"
                  class="search-size"
                >
                  <el-option
                    v-for="item in searchOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-col>
              <el-col :span="14">
                <el-input
                  placeholder=""
                  v-model="searchInput"
                  :suffix-icon="Search"
                  class="search-input"
                >
                </el-input>
              </el-col>
              <el-col :span="6">
                <el-button
                  type="primary"
                  class="search-button"
                  @click="searchButton"
                >
                  
                </el-button>
              </el-col>
            </el-row>
          </el-col>
          <el-col :span="4" class="add-button-pane">
            <el-button
              type="success"
              :icon="Plus"
              class="add-button"
              @click="addFromButton(addBookFormRef)"
            >
              
            </el-button>
          </el-col>
        </el-row>
        <!--        -->
        <el-row class="book-table">
          <el-col>
            <el-table :data="books" height="100%" empty-text="">
              <el-table-column fixed prop="id" label="Id" width="50" />
              <el-table-column prop="groups" label="" />
              <el-table-column prop="name" label="" />
              <el-table-column prop="author" label="" />
              <el-table-column prop="press" label="" />
              <el-table-column prop="price" label="()" />
              <el-table-column prop="quantity" label="()" />
              <el-table-column prop="isbn" label="ISBN" />
              <el-table-column fixed="right" label="">
                <template #default="books">
                  <el-button
                    @click="editFromButton(editBookFormRef, books.row)"
                    type="text"
                    ></el-button
                  >
                  <el-button @click="deleteBookDialog(books.row)" type="text"
                    ></el-button
                  >
                </template>
              </el-table-column>
            </el-table>
            <el-pagination
              background
              v-model:current-page="pageNum"
              v-model:page-size="pageSize"
              layout="prev, pager, next"
              :total="pageTotal"
              @current-change="page"
            >
            </el-pagination>
          </el-col>
        </el-row>

        <!--          -->
        <el-dialog
          v-model="addBookFormVisible"
          title=""
          class="add-book-dialog"
          width="500px"
          :close-on-click-modal="false"
        >
          <el-form
            :model="addBookForm"
            :rules="bookRules"
            ref="addBookFormRef"
            class="add-book-form"
          >
            <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="groups"
            >
              <el-select
                v-model="addBookForm.groups"
                placeholder=""
              >
                <el-option
                  v-for="item in bookType"
                  :key="item.typeId"
                  :label="item.typeName"
                  :value="item.typeName"
                >
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="name"
            >
              <el-input
                v-model="addBookForm.name"
                autocomplete="off"
              ></el-input>
            </el-form-item>
            <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="author"
            >
              <el-input
                v-model="addBookForm.author"
                autocomplete="off"
              ></el-input>
            </el-form-item>
            <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="press"
            >
              <el-input
                v-model="addBookForm.press"
                autocomplete="off"
              ></el-input>
            </el-form-item>
            <el-form-item
              label="()"
              :label-width="formLabelWidth"
              prop="price"
            >
              <el-input
                v-model="addBookForm.price"
                autocomplete="off"
                maxlength="13"
              ></el-input>
            </el-form-item>
            <el-form-item
              label="()"
              :label-width="formLabelWidth"
              prop="quantity"
            >
              <el-input
                v-model.number="addBookForm.quantity"
                autocomplete="off"
                maxlength="3"
              ></el-input>
            </el-form-item>
            <el-form-item
              label="ISBN"
              :label-width="formLabelWidth"
              prop="isbn"
            >
              <el-input
                v-model.number="addBookForm.isbn"
                autocomplete="off"
                maxlength="13"
              ></el-input>
            </el-form-item>
          </el-form>
          <template #footer>
            <span class="dialog-footer">
              <el-button @click="addBookFormVisible = false"></el-button>
              <el-button type="primary" @click="addBookButton(addBookFormRef)">
                
              </el-button>
            </span>
          </template>
        </el-dialog>

        <!--        -->
        <el-dialog
          v-model="editBookFormVisible"
          title=""
          class="edit-book-dialog"
          width="500px"
          :close-on-click-modal="false"
        >
          <el-form
            :model="editBookForm"
            :rules="bookRules"
            ref="editBookFormRef"
            class="edit-book-form"
          >
            <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="groups"
            >
              <el-select
                v-model="editBookForm.groups"
                placeholder=""
              >
                <el-option
                  v-for="item in bookType"
                  :key="item.typeId"
                  :label="item.typeName"
                  :value="item.typeName"
                >
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="name"
            >
              <el-input
                v-model="editBookForm.name"
                autocomplete="off"
              ></el-input>
            </el-form-item>
            <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="author"
            >
              <el-input
                v-model="editBookForm.author"
                autocomplete="off"
              ></el-input>
            </el-form-item>
            <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="press"
            >
              <el-input
                v-model="editBookForm.press"
                autocomplete="off"
              ></el-input>
            </el-form-item>
            <el-form-item
              label="()"
              :label-width="formLabelWidth"
              prop="price"
            >
              <el-input
                v-model="editBookForm.price"
                autocomplete="off"
                maxlength="13"
              ></el-input>
            </el-form-item>
            <el-form-item
              label="()"
              :label-width="formLabelWidth"
              prop="quantity"
            >
              <el-input
                v-model.number="editBookForm.quantity"
                autocomplete="off"
                maxlength="3"
              ></el-input>
            </el-form-item>
            <el-form-item
              label="ISBN"
              :label-width="formLabelWidth"
              prop="isbn"
            >
              <el-input
                v-model.number="editBookForm.isbn"
                autocomplete="off"
                maxlength="13"
              ></el-input>
            </el-form-item>
          </el-form>
          <template #footer>
            <span class="dialog-footer">
              <el-button @click="editBookFormVisible = false"></el-button>
              <el-button
                type="primary"
                @click="editBookButton(editBookFormRef)"
              >
                
              </el-button>
            </span>
          </template>
        </el-dialog>

        <!--        -->
        <el-dialog
          v-model="deleteBookDialogVisible"
          title=""
          width="500px"
        >
          <span> {{ deleteName }}</span>
          <template #footer>
            <span class="dialog-footer">
              <el-button @click="deleteBookDialogVisible = false"
                ></el-button
              >
              <el-button type="danger" @click="deleteBook">  </el-button>
            </span>
          </template>
        </el-dialog>
      </el-main>
    </el-container>
  </div>
</template>

<script lang="ts" setup>
import { reactive, ref } from "vue";
import type { FormInstance, FormRules } from "element-plus";
import { Plus, Search } from "@element-plus/icons-vue";
import axios from "axios";
import { ElMessageBox } from "element-plus";
import router from "@/router";

// 
let formLabelWidth = 120;

// 
let books = ref();
const getBook = () => {
  axios
    .get("http://localhost:8888/book/" + pageNum.value + "/" + pageSize.value)
    .then((resp) => {
      books.value = resp.data.content;
      pageTotal.value = resp.data.totalElements;
    });
};

// 
let pageNum = ref(1);
let pageSize = ref(10);
let pageTotal = ref(0);
const page = (val: number) => {
  pageNum.value = val;
  if (searchInput.value == undefined) {
    getBook();
  } else {
    searchBook();
  }
};

// 
let sizeOptions = [
  {
    value: 10,
    label: "10/",
  },
  {
    value: 50,
    label: "50/",
  },
  {
    value: 100,
    label: "100/",
  },
];
// 
const changeSize = (value: number) => {
  pageSize.value = value;
  if (searchInput.value == undefined) {
    getBook();
  } else {
    searchBook();
  }
};

// 
let searchModel = ref("name");
let searchOptions = [
  {
    value: "name",
    label: "",
  },
  {
    value: "author",
    label: "",
  },
  {
    value: "press",
    label: "",
  },
  {
    value: "isbn",
    label: "ISBN",
  },
];
const changeSearch = (value: string) => {
  searchModel.value = value;
};

// 
const searchInput = ref();
// 
const searchButton = () => {
  pageNum.value = 1;
  searchBook();
};
// 
const searchBook = () => {
  if (searchInput.value != "") {
    axios
      .get(
        "http://localhost:8888/book/search/" +
          searchModel.value +
          "/" +
          searchInput.value +
          "/" +
          pageNum.value +
          "/" +
          pageSize.value
      )
      .then((resp) => {
        books.value = resp.data.content;
        pageTotal.value = resp.data.totalElements;
      });
  } else {
    getBook();
  }
};

// 
const checkISBN = (rule: any, value: any, callback: any) => {
  if (!value) {
    return callback(new Error("ISBN"));
  } else {
    if (!Number.isInteger(value)) {
      callback(new Error("ISBN"));
    } else {
      let isbnReg = /^[1-9]\d{12}$/;
      if (!isbnReg.test(value)) {
        callback(new Error("13ISBN"));
      } else {
        callback();
      }
    }
  }
};
// 
const bookRules = reactive<FormRules>({
  groups: [{ required: true, message: "", trigger: "change" }],
  name: [{ required: true, message: "", trigger: "blur" }],
  author: [{ required: true, message: "", trigger: "blur" }],
  press: [{ required: true, message: "", trigger: "blur" }],
  price: [{ required: true, message: "()", trigger: "blur" }],
  quantity: [{ required: true, message: "()", trigger: "blur" }],
  isbn: [{ required: true, validator: checkISBN, trigger: "blur" }],
});

// 
let bookType = ref();
// 
const getBookType = () => {
  axios.get("http://localhost:8888/book/type").then((resp) => {
    bookType.value = resp.data;
  });
};

// 
let addBookFormVisible = ref(false);
// 
const addFromButton = (formEl: FormInstance | undefined) => {
  addBookFormVisible.value = true;
  if (!formEl) return;
  formEl.resetFields();
};

// 
const addBookFormRef = ref<FormInstance>();
let addBookForm = reactive({
  groups: "",
  name: "",
  author: "",
  press: "",
  price: "",
  quantity: "",
  isbn: "",
});

// 
const addBookButton = (formEl: FormInstance | undefined) => {
  if (!formEl) return;
  formEl.validate((valid) => {
    if (valid) {
      axios
        .post("http://localhost:8888/book/save", addBookForm)
        .then((resp) => {
          const statusCode = resp.data.statusCode;

          // 
          if (statusCode == 0) {
            ElMessageBox.alert("", "", {
              confirmButtonText: "",
            });
          }
          // 
          if (statusCode == 1) {
            ElMessageBox.alert("", "", {
              confirmButtonText: "",
              callback: () => {
                addBookFormVisible.value = false;
              },
            });
          }
          // ISBN
          if (statusCode == 2) {
            ElMessageBox.alert("ISBN", "", {
              confirmButtonText: "",
            });
          }
        });
    } else {
      return false;
    }
  });
};

// 
let editBookFormVisible = ref(false);
const editFromButton = (formEl: FormInstance | undefined, row: any) => {
  editBookFormVisible.value = true;
  editBookForm = reactive(JSON.parse(JSON.stringify(row)));
  editBookForm.isbn = Number(row.isbn);
  if (!formEl) return;
  formEl.resetFields();
};

// 
const editBookFormRef = ref<FormInstance>();
let editBookForm = reactive({
  groups: "",
  name: "",
  author: "",
  press: "",
  price: "",
  quantity: "",
  isbn: "",
});

// 
const editBookButton = (formEl: FormInstance | undefined) => {
  if (!formEl) return;
  formEl.validate((valid) => {
    if (valid) {
      axios
        .post("http://localhost:8888/book/update", editBookForm)
        .then((resp) => {
          const statusCode = resp.data.statusCode;

          // 
          if (statusCode == 0) {
            ElMessageBox.alert("", "", {
              confirmButtonText: "",
            });
          }
          // 
          if (statusCode == 1) {
            ElMessageBox.alert("", "", {
              confirmButtonText: "",
              callback: () => {
                editBookFormVisible.value = false;
              },
            });
          }
          // ISBN
          if (statusCode == 2) {
            ElMessageBox.alert("ISBN", "", {
              confirmButtonText: "",
            });
          }
        });
    } else {
      return false;
    }
  });
};

// 
let deleteName = ref("");
let deleteId = ref(0);
let deleteBookDialogVisible = ref(false);
const deleteBookDialog = (row: any) => {
  deleteId.value = row.id;
  deleteName.value = row.name;
  deleteBookDialogVisible.value = true;
};

// 
const deleteBook = () => {
  if (deleteId.value) {
    axios
      .post("http://localhost:8888/book/delete/" + deleteId.value)
      .then((resp) => {
        const statusCode = resp.data.statusCode;

        // 
        if (statusCode == 0) {
          ElMessageBox.alert("", "", {
            confirmButtonText: "",
          });
        }
        // 
        if (statusCode == 1) {
          ElMessageBox.alert("", "", {
            confirmButtonText: "",
            callback: () => {
              deleteBookDialogVisible.value = false;
            },
          });
        }
        // Id 
        if (statusCode == 2) {
          ElMessageBox.alert(" Id ", "", {
            confirmButtonText: "",
          });
        }
      });
  }
};

// 
const init = () => {
  // 
  getBook();
  // 
  getBookType();
};
init();
</script>

<style lang="scss">
@import "../assets/css/book";
</style>
