          :close-on-click-modal="false"
        >
          <el-form
            :model="editUserForm"
            :rules="userRules"
            ref="editUserFormRef"
            class="edit-user-form"
          >
            <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="groups"
            >
              <el-select
                v-model="editUserForm.groups"
                placeholder=""
              >
                <el-option
                  v-for="item in UserType"
                  :key="item.typeId"
                  :label="item.label"
                  :value="item.typeName"
                >
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item
              label=""
