<script lang="ts" setup>
import { reactive, ref } from "vue";
import type { FormInstance, FormRules } from "element-plus";
import { Plus, Search } from "@element-plus/icons-vue";
import axios from "axios";
import { ElMessageBox } from "element-plus";

// 
let formLabelWidth = 120;

// 
let users = ref();
const getUser = () => {
  axios
    .get("http://localhost:8888/user/" + pageNum.value + "/" + pageSize.value)
    .then((resp) => {
      console.log(resp);
      users.value = resp.data.content;
      pageTotal.value = resp.data.totalElements;
    });
};

// 
let pageNum = ref(1);
let pageSize = ref(10);
let pageTotal = ref(0);
const page = (val: number) => {
  pageNum.value = val;
  if (searchInput.value == undefined) {
    getUser();
  } else {
    searchUser();
  }
};

// 
let sizeOptions = [
  {
    value: 10,
    label: "10/",
  },
  {
    value: 50,
    label: "50/",
  },
  {
    value: 100,
    label: "100/",
  },
];
// 
const changeSize = (value: number) => {
  pageSize.value = value;
  if (searchInput.value == undefined) {
    getUser();
  } else {
    searchUser();
  }
};

// 
let searchModel = ref("username");
let searchOptions = [
  {
    value: "username",
    label: "",
  },
  {
    value: "idCard",
    label: "",
  },
  {
    value: "phone",
    label: "",
  },
];
const changeSearch = (value: string) => {
  searchModel.value = value;
};

// 
const searchInput = ref();
// 
const searchButton = () => {
  pageNum.value = 1;
  searchUser();
};
// 
const searchUser = () => {
  if (searchInput.value != "") {
    axios
      .get(
        "http://localhost:8888/user/search/" +
          searchModel.value +
          "/" +
          searchInput.value +
          "/" +
          pageNum.value +
          "/" +
          pageSize.value
      )
      .then((resp) => {
        users.value = resp.data.content;
        pageTotal.value = resp.data.totalElements;
      });
  } else {
    getUser();
  }
};

// 
const userRules = reactive<FormRules>({
  groups: [{ required: true, message: "", trigger: "change" }],
  username: [{ required: true, message: "", trigger: "blur" }],
  password: [{ required: true, message: "", trigger: "blur" }],
  name: [{ required: true, message: "", trigger: "blur" }],
  gender: [{ required: true, message: "", trigger: "change" }],
  idCard: [{ required: true, message: "", trigger: "blur" }],
  phone: [{ required: true, message: "", trigger: "blur" }],
  identity: [{ required: true, message: "", trigger: "change" }],
  bookCount: [
    { required: true, message: "", trigger: "blur" },
  ],
  state: [{ required: true, message: "", trigger: "change" }],
});

// 
const UserType = ref([
  {
    typeId: 1,
    typeName: "admin",
    label: "",
  },
  {
    typeId: 2,
    typeName: "user",
    label: "",
  },
]);
const GenderType = ref([
  {
    typeId: 1,
    typeName: "",
  },
  {
    typeId: 2,
    typeName: "",
  },
]);
const IdentityType = ref([
  {
    typeId: 1,
    typeName: "",
  },
  {
    typeId: 2,
    typeName: "",
  },
  {
    typeId: 3,
    typeName: "",
  },
]);
const StateType = ref([
  {
    id: 1,
    value: 1,
    label: "",
  },
  {
    id: 2,
    value: 0,
    label: "",
  },
]);

// 
let addUserFormVisible = ref(false);
// 
const addFromButton = (formEl: FormInstance | undefined) => {
  addUserFormVisible.value = true;
  if (!formEl) return;
  formEl.resetFields();
};

// 
const addUserFormRef = ref<FormInstance>();
let addUserForm = reactive({
  groups: "",
  username: "",
  password: "",
  name: "",
  gender: "",
  idCard: "",
  phone: "",
  identity: "",
});

// 
const addUserButton = (formEl: FormInstance | undefined) => {
  if (!formEl) return;
  formEl.validate((valid) => {
    if (valid) {
      axios
        .post("http://localhost:8888/user/save", addUserForm)
        .then((resp) => {
          const statusCode = resp.data.statusCode;

          // 
          if (statusCode == 0) {
            ElMessageBox.alert("", "", {
              confirmButtonText: "",
            });
          }
          // 
          if (statusCode == 1) {
            ElMessageBox.alert("", "", {
              confirmButtonText: "",
              callback: () => {
                addUserFormVisible.value = false;
              },
            });
          }
          // 
          if (statusCode == 2) {
            ElMessageBox.alert("", "", {
              confirmButtonText: "",
            });
          }
          // 
          if (statusCode == 3) {
            ElMessageBox.alert("", "", {
              confirmButtonText: "",
            });
          }
          // 
          if (statusCode == 4) {
            ElMessageBox.alert("", "", {
              confirmButtonText: "",
            });
          }
        });
    } else {
      return false;
    }
  });
};

// 
let editUserFormVisible = ref(false);
const editFromButton = (formEl: FormInstance | undefined, row: number) => {
  editUserFormVisible.value = true;
  editUserForm = reactive(JSON.parse(JSON.stringify(row)));
  if (!formEl) return;
  formEl.resetFields();
};

// 
const editUserFormRef = ref<FormInstance>();
let editUserForm = reactive({
  groups: "",
  username: "",
  password: "",
  name: "",
  gender: "",
  idCard: "",
  phone: "",
  identity: "",
  bookCount: 0,
  state: 1,
});

// 
const editUserButton = (formEl: FormInstance | undefined) => {
  if (!formEl) return;
  formEl.validate((valid) => {
    if (valid) {
      axios
        .post("http://localhost:8888/user/update", editUserForm)
        .then((resp) => {
          const statusCode = resp.data.statusCode;

          // 
          if (statusCode == 0) {
            ElMessageBox.alert("", "", {
              confirmButtonText: "",
            });
          }
          // 
          if (statusCode == 1) {
            ElMessageBox.alert("", "", {
              confirmButtonText: "",
              callback: () => {
                editUserFormVisible.value = false;
              },
            });
          }
          // 
          if (statusCode == 2) {
            ElMessageBox.alert("", "", {
              confirmButtonText: "",
            });
          }
        });
    } else {
      return false;
    }
  });
};

// 
let deleteName = ref("");
let deleteId = ref(0);
let deleteUserDialogVisible = ref(false);
const deleteUserDialog = (row: any) => {
  deleteId.value = row.id;
  deleteName.value = row.username;
  deleteUserDialogVisible.value = true;
};

// 
const deleteUser = () => {
  if (deleteId.value) {
    axios
      .post("http://localhost:8888/user/delete/" + deleteId.value)
      .then((resp) => {
        const statusCode = resp.data.statusCode;

        // 
        if (statusCode == 0) {
          ElMessageBox.alert("", "", {
            confirmButtonText: "",
          });
        }
        // 
        if (statusCode == 1) {
          ElMessageBox.alert("", "", {
            confirmButtonText: "",
            callback: () => {
              deleteUserDialogVisible.value = false;
            },
          });
        }
        // Id 
        if (statusCode == 2) {
          ElMessageBox.alert(" Id ", "", {
            confirmButtonText: "",
          });
        }
      });
  }
};

// 
const init = () => {
  getUser();
};
init();
</script>

