<template>
  <!--    -->
  <div class="login">
    <el-container>
      <el-main>
        <!--    -->
        <el-row class="login-form">
          <!--   -->
          <el-col :span="12" class="login-form-left">
            <p></p>
            <p></p>
          </el-col>
          <!--   -->
          <el-col :span="12" class="login-form-right">
            <!--    -->
            <el-form
              ref="loginFormRef"
              :model="loginForm"
              :rules="loginRules"
              @keyup.enter="login(loginFormRef)"
            >
              <el-form-item>
                <p></p>
              </el-form-item>
              <el-form-item prop="username">
                <el-input
                  v-model="loginForm.username"
                  placeholder=""
                ></el-input>
              </el-form-item>
              <el-form-item prop="password">
                <el-input
                  type="password"
                  v-model="loginForm.password"
                  placeholder=""
                ></el-input>
              </el-form-item>
              <el-form-item>
                <el-button
                  type="primary"
                  @click="login(loginFormRef)"
                  ref="loginButtonRef"
                  :disabled="loginState"
                  ></el-button
                >
              </el-form-item>
              <el-form-item>
                <el-button type="info" @click="register"></el-button>
              </el-form-item>
            </el-form>
          </el-col>
        </el-row>
      </el-main>
    </el-container>
  </div>
</template>

<script lang="ts" setup>
import { reactive, ref } from "vue";
import type { FormInstance, FormRules } from "element-plus";
import { ElMessageBox, ElButton } from "element-plus";

import axios from "axios";
import jsCookie from "js-cookie";
import router from "@/router";

// 
const userLoginState = jsCookie.get("username");
if (userLoginState != undefined) {
  router.push("/home");
}

// 
const loginButtonRef = ref();
let loginState = ref(false);

// 
const loginFormRef = ref<FormInstance>();
const loginForm = reactive({
  username: "",
  password: "",
});

const register = () => {
  router.push("/register");
};

// 
const loginRules = reactive<FormRules>({
  username: [{ required: true, message: "", trigger: "blur" }],
  password: [{ required: true, message: "", trigger: "blur" }],
});

// 
const login = (formEl: FormInstance | undefined) => {
  if (!formEl) return;
  formEl.validate((valid) => {
    if (valid && !loginState.value) {
      loginState.value = true;
      // 
      axios
        .post("http://localhost:8888/login/admin", loginForm)
        .then((resp) => {
          const statusCode = resp.data.statusCode;

          // 
          if (statusCode == 0) {
            ElMessageBox.alert("", "", {
              confirmButtonText: "",
            });
            loginState.value = false;
          }

          // 
          if (statusCode == 1) {
            ElMessageBox.alert("", "", {
              confirmButtonText: "",
              callback: () => {
                // Cookie
                jsCookie.set("username", resp.data.username, {
                  expires: 1,
                  path: "/",
                });
                // 
                router.push("/home");
              },
            });
          }

          // 
          if (statusCode == 2) {
            ElMessageBox.alert("", "", {
              confirmButtonText: "",
            });
            loginState.value = false;
          }
        });
    } else {
      return false;
    }
  });
};
</script>

<style lang="scss">
@import "../assets/css/login";
</style>
