<template>
  <!--    -->
  <div class="register">
    <el-container>
      <el-main>
        <!--    -->
        <el-row class="register-form">
          <!--   -->
          <el-col :span="12" class="register-form-left">
            <p></p>
            <p></p>
          </el-col>
          <!--   -->
          <el-col :span="12" class="register-form-right">
            <!--    -->
            <el-form
              ref="registerFormRef"
              :model="registerForm"
              :rules="registerRules"
              @keyup.enter="register(registerFormRef)"
            >
              <el-form-item>
                <p></p>
              </el-form-item>
              <el-form-item prop="username">
                <el-input
                  v-model="registerForm.username"
                  placeholder=""
                ></el-input>
              </el-form-item>
              <el-form-item prop="password">
                <el-input
                  type="password"
                  v-model="registerForm.password"
                  placeholder=""
                ></el-input>
              </el-form-item>
              <el-form-item prop="confirmPassword">
                <el-input
                  type="password"
                  v-model="registerForm.confirmPassword"
                  placeholder=""
                ></el-input>
              </el-form-item>
              <el-form-item prop="email">
                <el-input
                  v-model="registerForm.email"
                  placeholder=""
                ></el-input>
              </el-form-item>
              <el-form-item prop="sendVerificationCode">
                <el-button
                  type="primary"
                  @click="sendVerificationCode"
                  size="small"
                >
                  
                </el-button>
              </el-form-item>
              <el-form-item prop="verificationCode">
                <el-input
                  v-model="registerForm.code"
                  placeholder=""
                ></el-input>
              </el-form-item>
              <el-form-item>
                <el-button
                  type="primary"
                  @click="register(registerFormRef)"
                  ref="registerButtonRef"
                  :disabled="registerState"
                  ></el-button
                >
              </el-form-item>
              <el-form-item>
                <el-button type="info" @click="login"></el-button>
              </el-form-item>
            </el-form>
          </el-col>
        </el-row>
      </el-main>
    </el-container>
  </div>
</template>

<script lang="ts" setup>
import { reactive, ref } from "vue";
import type { FormInstance, FormRules } from "element-plus";
import { ElMessageBox, ElButton } from "element-plus";

import axios from "axios";
import jsCookie from "js-cookie";
import router from "@/router";

// 
const registerButtonRef = ref();
let registerState = ref(false);

// 
const registerFormRef = ref<FormInstance>();
const registerForm = reactive({
  username: "",
  email: "",
  password: "",
  confirmPassword: "",
  code: "",
});

const login = () => {
  router.push("/login");
};

// 
const registerRules = reactive<FormRules>({
  username: [{ required: true, message: "", trigger: "blur" }],
  email: [
    { required: true, message: "", trigger: "blur" },
    { type: "email", message: "", trigger: "blur" },
  ],
  password: [{ required: true, message: "", trigger: "blur" }],
  confirmPassword: [
    { required: true, message: "", trigger: "blur" },
    {
      validator: (rule, value, callback) => {
        if (value !== registerForm.password) {
          callback(new Error(""));
        } else {
          callback();
        }
      },
      trigger: "blur",
    },
  ],
});

// 
const sendVerificationCode = () => {
  if (!registerForm.email) {
    ElMessageBox.alert("", "", {
      confirmButtonText: "",
    });
    return;
  }
  axios
    .post("http://localhost:8888/register/sendVerificationCode", registerForm)
    .then((resp) => {
      const statusCode = resp.data.statusCode;

      // 
      if (statusCode == 0) {
        ElMessageBox.alert("", "", {
          confirmButtonText: "",
        });
      }

      // 
      if (statusCode == 1) {
        ElMessageBox.alert("", "", {
          confirmButtonText: "",
        });
      }
    });
};

// 
const register = (formEl: FormInstance | undefined) => {
  if (!formEl) return;
  formEl.validate((valid) => {
    if (valid && !registerState.value) {
      registerState.value = true;
      // 
      axios
        .post("http://localhost:8888/register/admin", registerForm)
        .then((resp) => {
          const statusCode = resp.data.statusCode;

          // 
          if (statusCode == 0) {
            ElMessageBox.alert("", "", {
              confirmButtonText: "",
            });
            registerState.value = false;
          }
          if (statusCode == 1) {
            ElMessageBox.alert("", "", {
              confirmButtonText: "",
            });
            registerState.value = false;
          }
          // 
          if (statusCode == 2) {
            ElMessageBox.alert("", "", {
              confirmButtonText: "",
              callback: () => {
                // Cookie
                jsCookie.set("username", resp.data.username, {
                  expires: 1,
                  path: "/",
                });
                // 
                router.push("/home");
              },
            });
          }
        });
    } else {
      return false;
    }
  });
};
</script>

<style lang="scss">
@import "../assets/css/register";
</style>
