<template>
  <div class="borrow">
    <el-container>
      <el-main>
        <!--        -->
        <el-row class="borrow-pane">
          <el-col>
            <el-col><span class="title"></span></el-col>
            <!--            -->
            <el-col class="search-book-pane"
              ><el-input
                class="search-book-input"
                placeholder="ISBN"
                v-model="searchBookInput"
                maxlength="13"
              ></el-input
              ><el-button
                class="search-book-button"
                type="primary"
                @click="searchBook"
                ></el-button
              ></el-col
            >
          </el-col>
          <el-col>
            <el-row>
              <!--              -->
              <el-col :span="12" class="book-info-pane">
                <span class="title"></span>
                <span class="sub-title">{{ book.name }}</span>
                <span class="sub-title">{{ book.author }}</span>
                <span class="sub-title">{{ book.press }}</span>
                <span class="sub-title">ISBN{{ book.isbn }}</span>
                <span class="sub-title">{{ book.quantity }}</span>
              </el-col>
              <!--              -->
              <el-col :span="12" class="user-form-pane">
                <el-form v-model="userInfo" ref="userInfoRef">
                  <span class="title"></span>
                  <el-input
                    placeholder=""
                    v-model="userInfo.name"
                  ></el-input>
                  <el-input
                    placeholder=""
                    v-model="userInfo.idCard"
                  ></el-input>
                  <el-input
                    placeholder=""
                    v-model="userInfo.phone"
                  ></el-input>
                  <el-button type="primary" @click="borrowBook"
                    ></el-button
                  >
                </el-form>
              </el-col>
            </el-row>
          </el-col>
        </el-row>
      </el-main>
    </el-container>
  </div>
</template>

<script lang="ts" setup>
import { reactive, ref } from "vue";
import type { FormInstance } from "element-plus";
import axios from "axios";
import { ElMessageBox } from "element-plus";

// 
const searchBookInput = ref();
const searchBook = () => {
  axios
    .get("http://localhost:8888/book/find/isbn/" + searchBookInput.value)
    .then((resp) => {
      book.value = resp.data;
    });
};

// 
let book = ref([
  {
    name: "",
    author: "",
    press: "",
    isbn: "",
    quantity: "",
  },
]);

// 
const userInfoRef = ref<FormInstance>();
const userInfo = reactive({
  name: "",
  idCard: "",
  phone: "",
});

// 
const borrowBook = () => {
  axios
    .post("http://localhost:8888/borrow/" + searchBookInput.value, userInfo)
    .then((resp) => {
      const statusCode = resp.data.statusCode;

      // 
      if (statusCode == 0) {
        ElMessageBox.alert("", "", {
          confirmButtonText: "",
        });
      }

      // 
      if (statusCode == 1) {
        ElMessageBox.alert("", "", {
          confirmButtonText: "",
        });
      }

      // 
      if (statusCode == 2) {
        ElMessageBox.alert("", "", {
          confirmButtonText: "",
        });
      }

      // 
      if (statusCode == 3) {
        ElMessageBox.alert("", "", {
          confirmButtonText: "",
        });
      }

      // 
      if (statusCode == 4) {
        ElMessageBox.alert("", "", {
          confirmButtonText: "",
        });
      }
    });
};
</script>

<style lang="scss">
@import "../assets/css/borrow";
</style>
