<template>
  <div class="nav">
    <el-container class="layout-container-demo" style="height: 100%">
      <el-aside>
        <!--        -->
        <el-scrollbar>
          <div class="title"><p></p></div>
          <el-menu router :default-active="$route.path" unique-opened>
            <el-menu-item index="/home" @click="addTab('', '/home')"
              ><el-icon><House /></el-icon></el-menu-item
            >
            <el-sub-menu
              v-for="(item, index) in this.$router['options'].routes"
              :key="index + 1"
              :index="index + ''"
              v-show="item.meta.show"
            >
              <template #title>
                <el-icon v-if="item.name === ''"><Reading /></el-icon>
                <el-icon v-if="item.name === ''"><Setting /></el-icon>
                <el-icon v-if="item.name === ''"><User /></el-icon>
                <p>{{ item.name }}</p>
              </template>
              <el-menu-item
                v-for="(item2, index2) in item.children"
                :key="index2"
                :index="item2.path"
                v-show="item2.meta.show"
                @click="addTab(item2.name, item2.path)"
              >
                {{ item2.name }}
              </el-menu-item>
            </el-sub-menu>
            <el-menu-item @click="loginOut"
              ><el-icon><Back /></el-icon></el-menu-item
            >
          </el-menu>
        </el-scrollbar>
      </el-aside>

      <el-main>
        <!--        -->
        <div class="toolbar">
          <span>{{ userInfo.username }}</span>
          <el-dropdown>
            <el-icon><Setting /></el-icon>
            <template #dropdown>
              <el-dropdown-menu>
                <el-dropdown-item @click="loginOut"></el-dropdown-item>
              </el-dropdown-menu>
            </template>
          </el-dropdown>
        </div>
        <!--        -->
        <div class="tabs">
          <el-tabs
            v-model="editableTabsValue"
            @tab-click="tabClick"
            @tab-remove="tabRemove"
          >
            <el-tab-pane
              v-for="item in editableTabs"
              :label="item.name"
              :key="item.path"
              :name="item.path"
              :closable="item.closable"
            >
              <router-view
                :key="item.path"
                :name="item.routerName"
              ></router-view>
            </el-tab-pane>
          </el-tabs>
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script lang="ts" setup>
import { ref } from "vue";
import { House, Reading, User, Setting, Back } from "@element-plus/icons-vue";
import router from "../router/index";
import jsCookie from "js-cookie";
import { ElMessageBox } from "element-plus";

// 
const userLoginState = jsCookie.get("username");
if (userLoginState == undefined) {
  ElMessageBox.alert("", "", {
    confirmButtonText: "",
    callback: () => {
      // 
      router.push("/login");
    },
  });
}

// 
const userInfo = ref({
  username: jsCookie.get("username"),
});

// 
const loginOut = () => {
  ElMessageBox.alert("", "", {
    confirmButtonText: "",
    callback: () => {
      // 
      router.push("/login");
      // cookie
      jsCookie.remove("username");
    },
  });
};

// 
const addTab = (name: any, path: any) => {
  let isExist = false;
  // 
  for (const item of editableTabs.value) {
    if (item.name == name && item.path == path) {
      isExist = true;
      break;
    }
  }

  // 
  if (!isExist) {
    editableTabs.value.push({
      name: name,
      path: path,
      closable: true,
      routerName: path.substring(1),
    });
  }

  // 
  editableTabsValue.value = path;
};

//
let editableTabsValue = ref("/home");
const editableTabs = ref([
  {
    name: "",
    path: "/home",
    closable: false,
    routerName: "home",
  },
]);

// 
const tabClick = (tab: any) => {
  router.push(tab.props.name);
};

// 
const tabRemove = (targetName: string) => {
  const tabs = editableTabs.value;
  let activeName = editableTabsValue.value;
  if (activeName === targetName) {
    tabs.forEach((tab, index) => {
      if (tab.path === targetName) {
        const nextTab = tabs[index + 1] || tabs[index - 1];
        if (nextTab) {
          activeName = nextTab.path;
        }
      }
    });
  }

  // 
  router.push(activeName);
  editableTabsValue.value = activeName;
  editableTabs.value = tabs.filter((tab) => tab.path !== targetName);
};

// 
const tabName = router.currentRoute.value.name;
const tabPath = router.currentRoute.value.path;
addTab(tabName, tabPath);
</script>

<style lang="scss">
@import "../assets/css/nav";
</style>
